import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllRfqsComponent } from './pages/all-rfqs/all-rfqs.component';
import { ManageRfqsComponent } from './pages/manage-rfqs/manage-rfqs.component';
import { ApprovedRfqsComponent } from './pages/approved-rfqs/approved-rfqs.component';
import { RoutePrivilegeGuard } from '../../data/services/_AccessControlAuthGuard.service';
import { RfqsPendingCommitteeComponent } from './pages/rfqs-pending-committee/rfqs-pending-committee.component';

const routes: Routes = [
  {
    path: "approved-needs",
    component: ApprovedRfqsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["Approved Needs"] },
  },

  {
    path: "all-rfqs",
    component: AllRfqsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["All Rfqs"] },
  },
  {
    path: "pending-committee",
    component: RfqsPendingCommitteeComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["Rfqs Pending Committee"] },
  },
  {
    path: "manage-rfqs",
    component: ManageRfqsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["Manage Rfqs"] },
  },
];



@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RfqsRoutingModule { }
