  deleteRFQ(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "Do you really want to delete this RFQ?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Delete RFQ!",
    }).then((result) => {
      if (result.isConfirmed) {
        const params = new HttpParams()
          .set("id", id)
          .set("username", this.currentUser);
        this.rfqsService
          .deleteRFQsTemporarily(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              this.snackbar.showNotification(
                "snackbar-success",
                "RFQ Deleted Successfully"
              );
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
            },
            complete: () => {
              this.getData(this.currentPage, this.pageSize);
            },
          }),
          Subscription;
      }
    });
  }

  cancelReactivateRFQ(rfqCode: any, action: any) {
    let confirmationTitle = "Are you sure?";
    let confirmationText = "Do you really want to ";
    let confirmationButtonText = "Yes, ";

    if (action === "CANCELED") {
      confirmationText += "cancel this RFQ?";
      confirmationButtonText += "Cancel RFQ!";
    } else if (action === "PENDING") {
      confirmationText += "reactivate this RFQ?";
      confirmationButtonText += "Reactivate RFQ!";
    }

    Swal.fire({
      title: confirmationTitle,
      text: confirmationText,
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: confirmationButtonText,
    }).then((result) => {
      if (result.isConfirmed) {
        const params = new HttpParams()
          .set("rfqCode", rfqCode)
          .set("status", action);

        this.rfqsService
          .cancelOrReactivateRFQ(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              if (action === "CANCELED") {
                this.snackbar.showNotification(
                  "snackbar-success",
                  "RFQ Canceled Successfully"
                );
              } else if (action === "PENDING") {
                this.snackbar.showNotification(
                  "snackbar-success",
                  "RFQ Reactivated Successfully"
                );
              }
              this.getData(this.currentPage, this.pageSize);
            },
            error: (err) => {
              this.snackbar.showNotification(
                "snackbar-danger",
                "Server Error: " + err.message
              );
            },
          });
      }
    });
  }

