import { SelectionModel } from '@angular/cdk/collections';
import { DatePipe } from '@angular/common';
import { HttpParams } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatMenuTrigger } from '@angular/material/menu';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { Subscription, Subject, takeUntil } from 'rxjs';
import { NeedService } from 'src/app/erp-procurement/data/services/need.service';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-approved-rfqs',
  templateUrl: './approved-rfqs.component.html',
  styleUrls: ['./approved-rfqs.component.sass']
})
export class ApprovedRfqsComponent implements OnInit {

  displayedColumns: string[] = ['id', 'needCode', 'needTitle', 'needDescription', 'status', 'postedBy', 'postedTime', 'hodApproval', 'procurementApproval', 'action'];
  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  contextMenu: MatMenuTrigger;
  contextMenuPosition = { x: "0px", y: "0px" };
  subscription!: Subscription;
  selection = new SelectionModel<any>(true, []);
  data: any;
  error: any;
  formData: any;
  isLoading = true;
  pageFunction = "Update";
    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  Form: FormGroup;
  currentDate: Date = new Date();
  dateTomorrow: Date = new Date(
    this.currentDate.getTime() + 1 * 24 * 60 * 60 * 1000
  );
  oneWeekAgo: Date = new Date(
    this.currentDate.getTime() - 7 * 24 * 60 * 60 * 1000
  );

  reqStatuses: any[] = [{ name: "PENDING" }, { name: "APPROVED" }, { name: "RETURNED" }, { name: "REJECTED" }]



  constructor(
    private router: Router,
    private snackbar: SnackbarService,
    private needService: NeedService,
    private fb: FormBuilder,
    private datepipe: DatePipe
  ) { }

  ngOnInit(): void {
    this.Form = this.fb.group({
      //status: ["PENDING", Validators.required],
      hodStatus: ["APPROVED", Validators.required],
      procurementStatus: ["APPROVED", Validators.required],
      fromDate: [this.oneWeekAgo, Validators.required],
      toDate: [this.dateTomorrow, Validators.required],
    });
    this.getData();

  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }



  getData() {
    let fromDate = this.datepipe.transform(
      this.Form.value.fromDate,
      "YYYY-MM-dd"
    );
    let toDate = this.datepipe.transform(
      this.Form.value.toDate,
      "YYYY-MM-dd"
    );
    const params = new HttpParams()
     // .set("status", this.Form.value.status)
      .set("hod_status", this.Form.value.hodStatus)
      .set("procurement_status", this.Form.value.procurementStatus)
      .set("fromDate", fromDate)
      .set("toDate", toDate)


    this.needService.getNeedsByStatus(params).pipe(takeUntil(this.destroy$)).subscribe(
      {
        next: (res) => {
          if (res.entity) {
            this.data = res.entity;
            this.isLoading = false;
            this.dataSource = new MatTableDataSource(this.data);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;

            console.log("this.data :", this.data)

          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
              "No Data Available"
            )
          }
        },
        error: (err) => {
          this.snackbar.showNotification(
            "snackbar-danger",
            "No Needs Matching these parameters!"
          );
        },
        complete: () => {

        }

      }
    ), Subscription;
   


  }

  getSelectedStatus(event: any) {

    this.getData();
  }

  addNeed() {
    this.router.navigate(["/erp-procurement/needs/manage-need"]);
  }

  refresh() {
    this.getData();
  }

  editNeed(data: any) {
    const additionalData = data;
    //const serializedData = JSON.stringify(additionalData);

    let route = '/erp-procurement/needs/manage-need';
    this.router.navigate([route], { queryParams: { id: data.id, action: 'Update' } });
  }
  viewNeed(data: any) {
    const additionalData = data;
    //const serializedData = JSON.stringify(additionalData);
    let route = '/erp-procurement/needs/manage-need';
    this.router.navigate([route], { queryParams: { id: data.id, action: 'View' } });
  }
  validateNeed(data: any) {
    const additionalData = data;
   // const serializedData = JSON.stringify(additionalData);
    let route = '/erp-procurement/needs/manage-need';
    this.router.navigate([route], { queryParams: { id: data.id, action: 'Validate' } });
  }

  deleteNeed(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "Do you really want to delete this Need?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Delete Need!",
    }).then((result) => {
      if (result.isConfirmed) {

        this.needService.deleteNeedsTemporarily(id).pipe(takeUntil(this.destroy$)).subscribe(
          {
            next: (res) => {
              this.snackbar.showNotification(
                "snackbar-success",
                "Need Deleted Successfully"
              );
            },
            error: (err) => {
              this.snackbar.showNotification(
                "snackbar-danger",
                err.message
              );
            },
            complete: () => {
              this.getData();
            }

          }
        ), Subscription;

      }
    });
  }























}