import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, map } from 'rxjs';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class RfqsService {

  constructor(private http: HttpClient) { }

  getRfq(): Observable<any> {
    const RfqUrl = `${environment.baseUrlProcurement}/api/v1/rfq/all`;
    return this.http.get<any[]>(RfqUrl);
  }
  getRfqById(params: any): Observable<any> {
    return this.http.get<any>(`${environment.baseUrlProcurement}/api/v1/rfq/fetchBy/id`, { params });
  }

  addRfq(data: any): Observable<any> {
    const RfqUrl = `${environment.baseUrlProcurement}/api/v1/rfq/create`;
    return this.http.post<any>(RfqUrl, data);
  }

  updateRfq(RfqDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/rfq/update`;
    return this.http.put<any>(updateBillUrl, RfqDetails)
  }

  updateRfqCommetteeApproval(params: any, approvalAttachments: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/rfq/commetteeApproval`;
    return this.http.put<any>(updateBillUrl, approvalAttachments, { params })
  }




  cancelOrReactivateRFQ(params: any): Observable<any> {
    const RfqUrl = `${environment.baseUrlProcurement}/api/v1/rfq/cancel`;
    return this.http.post<any>(RfqUrl, {}, { params });
  }


  deleteRFQsTemporarily(params: any): Observable<any> {
    const RfqUrl = `${environment.baseUrlProcurement}/api/v1/rfq/delete`;
    return this.http.delete<any>(RfqUrl, { params });
  }


  getRFQByStatus(params: any): Observable<any> {
    const rfqssUrl = `${environment.baseUrlProcurement}/api/v1/rfq/rfq/status`;
    return this.http.get<any>(rfqssUrl, { params: params, });
  }



  validateRFQ(statusDetails: any): Observable<any> {
    const API_URL = `${environment.baseUrlProcurement}/api/v1/rfq/verify`;
    return this.http.put(
      API_URL,
      statusDetails
    );
  }


  processSupplierAwardLetter(params: any): Observable<any> {
    const RfqUrl = `${environment.baseUrlProcurement}/api/v1/rfq/send/award`;
    return this.http.post<any>(RfqUrl, {}, { params });
  }

  // 
  processEmailForNomination(params: any): Observable<any> {
    const RFPUrl = `${environment.baseUrlProcurement}/api/v1/rfq/send/award/notification`;
    return this.http.post<any>(RFPUrl, {}, { params });
  }



  getRfqPendingCommitee(): Observable<any> {
    const RfqUrl = `${environment.baseUrlProcurement}/api/v1/rfq/all/req/committee/approval`;
    return this.http.get<any[]>(RfqUrl);
  }

  // ********************************************************************************************************************

  donwloadRFQRecipientsReport(params: any): Observable<any> {
    let headers = new HttpHeaders();
    headers.append("Accept", "application/pdf");
    let requestOptions: any = {
      params: params,
      headers: headers,
      responseType: "blob",
      withCredentials: false,
    };
    let API_URL = `${environment.baseUrlProcurement}/api/v1/report/download/rfq/suppliers`;
    return this.http.get(API_URL, requestOptions).pipe(
      map((response: BlobPart) => {
        console.log("Hey this is download RFQ recipients Report", response);
        return {
          filename: "Report",
          data: new Blob([response], { type: "application/pdf" }),
        };
      })
    );
  }
  donwloadRFQItemsReport(params: any): Observable<any> {
    let headers = new HttpHeaders();
    headers.append("Accept", "application/pdf");
    let requestOptions: any = {
      params: params,
      headers: headers,
      responseType: "blob",
      withCredentials: false,
    };
    let API_URL = `${environment.baseUrlProcurement}/api/v1/report/download/rfq/items`;
    return this.http.get(API_URL, requestOptions).pipe(
      map((response: BlobPart) => {
        console.log("Hey this is download RFQ Items Report", response);
        return {
          filename: "Report",
          data: new Blob([response], { type: "application/pdf" }),
        };
      })
    );
  }


}
