import { SelectionModel } from "@angular/cdk/collections";
import { Component, Inject, OnInit } from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
} from "@angular/forms";
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialogConfig,
  MatDialog,
} from "@angular/material/dialog";
import { Subscription } from "rxjs";
import { TaxLookupComponent } from "src/app/erp-finance/data/lookups/tax-lookup/tax-lookup.component";
import { CalculationService } from "src/app/erp-procurement/data/services/_procurement-calculations.service";
import { ManageProcurementCommitteesComponent } from "src/app/erp-procurement/modules/parameters/pages/procurement-committee/manage-procurement-committees/manage-procurement-committees.component";

@Component({
  selector: "app-manage-rfq-item",
  templateUrl: "./manage-rfq-item.component.html",
  styleUrls: ["./manage-rfq-item.component.sass"],
})
export class ManageRfqItemComponent implements OnInit {
  selection = new SelectionModel<any>(true, []);

  loading = false;
  members: any;
  bgtForm: FormGroup;

  constructor(
    public dialogRef: MatDialogRef<ManageProcurementCommitteesComponent>,
    private fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private dialog: MatDialog,
    private calculationService: CalculationService
  ) {}
  subscription!: Subscription;

  pageAction = "Add";

  ngOnInit(): void {
    this.pageAction = this.data.action;
    this.generateRfqItemForm(this.data?.data);
    this.calculateTax();
  }

  generateRfqItemForm(data?: any) {
    this.bgtForm = this.fb.group({
      id: [data?.id || ""],
      itemName: [data?.itemName || "", [Validators.required]],
      itemCode: [data?.itemCode || ""],
      description: [data?.description || "", [Validators.maxLength(300)]],
      unitOfMeasure: [data?.unitOfMeasure || "", [Validators.required]],
      quantity: [
        data?.quantity || 0,
        [
          Validators.required,
          Validators.min(0.01),
          //Validators.pattern("^[0-9]+(\\.[0-9]{1,2})?$"),
          Validators.pattern("^[0-9]+(\\.[0-9]{1,2})?$|^$"),
        ],
      ],
      unitPricePerItem: [
        data?.unitPricePerItem || 0,
        [
          Validators.required,
          Validators.max(1000000000),
          Validators.pattern(/^\d+(\.\d{1,2})?$/),
        ],
      ],
      negotiatedPrice: [
        data?.negotiatedPrice || 0,
        [Validators.max(1000000000), Validators.pattern(/^\d+(\.\d{1,2})?$/)],
      ],
      finalPrice: [
        data?.finalPrice || 0,
        [
          Validators.required,
          Validators.max(1000000000),
          Validators.pattern(/^\d+(\.\d{1,2})?$/),
        ],
      ],
      needByDate: [
        data?.needByDate || "",
        [Validators.required, this.dateValidator],
      ],
      taxRate: [
        data?.taxRate || 0,
        [
          Validators.required,
          Validators.pattern("^[0-9]+(\\.[0-9]{1,2})?$|^$"),
        ],
      ],
      taxName: [data?.taxName || "", [Validators.required]],
      subTotal: [data?.subTotal || 0],
      taxAmount: [data?.taxAmount || 0],
      totalAmount: [data?.totalAmount || 0],
    });

    // Subscribe to value changes for unitPricePerItem
    this.bgtForm.get("unitPricePerItem").valueChanges.subscribe((unitPricePerItem) => {
      this.bgtForm.get("finalPrice").setValue(unitPricePerItem, { emitEvent: false });
      this.calculateTax();
    });

    // Subscribe to value changes for negotiatedPrice
    this.bgtForm
      .get("negotiatedPrice")
      .valueChanges.subscribe((negotiatedPrice) => {
        this.bgtForm
          .get("finalPrice")
          .setValue(negotiatedPrice, { emitEvent: false });
        this.calculateTax();
      });
  }

  doCalculation(event: Event) {
    this.calculateTax();
  }

  taxDetails = { subTotal: 0, taxAmount: 0, totalAmount: 0 };

  calculateTax() {
    this.taxDetails = { subTotal: 0, taxAmount: 0, totalAmount: 0 };
    const quantity = Number(this.bgtForm.value.quantity);
    const finalPrice = Number(this.bgtForm.value.finalPrice);
    const taxRate = Number(this.bgtForm.value.taxRate);

    console.log("quantity: ", quantity);
    console.log("taxRate: ", taxRate);
    console.log("finalPrice: ", finalPrice);

    console.log("*************************************");

    this.taxDetails = this.calculationService.calculateTax(
      quantity,
      finalPrice,
      taxRate
    );

    this.bgtForm.patchValue({
      subTotal: this.taxDetails.subTotal.toFixed(2),
      taxAmount: this.taxDetails.taxAmount.toFixed(2),
      totalAmount: this.taxDetails.totalAmount.toFixed(2),
    });

    console.log("subTotal: ", this.bgtForm.value.subTotal);
    console.log("taxAmount: ", this.bgtForm.value.taxAmount);
    console.log("totalAmount: ", this.bgtForm.value.totalAmount);
  }

  dateValidator(control: FormControl) {
    const selectedDate: Date = control.value;
    const today: Date = new Date();

    if (selectedDate < today) {
      return { pastDate: true };
    }

    return null;
  }

  taxIsSelected = false;
  selectedTaxes: any[] = [];
  taxesLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "single tax",
      selected: this.selectedTaxes,
    };

    const dialogRef = this.dialog.open(TaxLookupComponent, dialogConfig);

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data);

        this.bgtForm.patchValue({
          taxRate: result.data[0].taxRate,
          taxName: result.data[0].taxName,
        });
      }
    });
  }

  onSubmit() {
    console.log("this.bgtForm.value: ", this.bgtForm.value);

    this.dialogRef.close({ event: "close", item: this.bgtForm.value });
  }
  onClose() {
    this.dialogRef.close();
  }
}
